<template>
    <v-container fluid class=" fs-14-vw qiong-pt-30">
    
        <qiong-header :space="`qiong-pt-20`">活跃分店</qiong-header>
        
        <qiong-page-panel>
            <bonus-active-house-table-header></bonus-active-house-table-header>
            <div class="qiong-pt-15"></div>
            <bonus-active-house-table :childs="childrens" v-if="childrens != null"></bonus-active-house-table>
            <loading v-else></loading>
        </qiong-page-panel>
    </v-container>
</template>

<script>
import Loading from '../../../components/Data/Loading.vue'
import api_bonus from '../../../common/es/api_bonus/index'

import QiongHeader from '../../../components/Qiong/Pager/QiongHeader.vue'
import QiongPagePanel from '../../../components/Qiong/QiongPagePanel.vue'

import BonusActiveHouseTable from './Inner/BonusActiveHouseTable.vue'
import BonusActiveHouseTableHeader from './Inner/BonusActiveHouseTableHeader.vue'

    export default {
        components: {
            QiongHeader,
                QiongPagePanel,
                BonusActiveHouseTableHeader,
                BonusActiveHouseTable,
                Loading
        },
        name: '',
        data() {
            return {
                childrens: null
            }
        },
        mounted() {
            this.switchChidren()
        },
        methods: {
            switchChidren() {
                const _this = this
                const many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer
                if (many) {

                    this.childrens = [ ]
                    api_bonus._build_res(many, transfer, function (u, t) {
                        _this.childrens.push(u)
                    })
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


