            <div class="qiong-wide-9">&nbsp;</div>
            <div class="qiong-wide-13">{{ v.username }}</div>
            <div class="qiong-wide-11">{{ v.number_code }}</div>
            <div class="qiong-wide-27">{{ v.display_name }}</div>
            <div class="qiong-wide-9">{{ v.ipv }}</div>
            <div class="qiong-wide-12">
                <level-viewing :item="v.vip.name + '_' + v.vip.star"></level-viewing>
            </div>
            <div class="qiong-wide-17">{{ v.bonus.usd_active_house }}</div>
        </div>
    
    </div>
</template>

<script>
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
    export default {
  components: {LevelViewing  },
        props: {
            childs: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


